.meinButtonBox {
  display: flex;
  justify-content: center;
  align-items: center;

  padding: 16px 33px;
  border-radius: 12px;
  border: none;

  color: var(--main-black);
  font-family: Roboto;
  font-size: 18px;
  font-style: normal;
  font-weight: 700;
  line-height: 1.55;

  @media screen and (min-width: 500px) {
    padding: 16px 88px;
  }
}
